import { useReducer, useState } from "react";
import styled from "styled-components";
const Ul = styled.ul`
    display:flex;
    flex-direction:column;
`;

const Title = styled.h1`
    font-size:30px;
    margin-bottom:10px;
    color:teal;
`;
const Li = styled.li`
    display:flex;
    align-items:center;
`;
const Button = styled.button`
    width:35px;
    height:35px;
    border-radius:10px;
`;
const Hone = styled.h1`
    margin-right:10px;
    font-size: 24px;
    margin-bottom:10px;
`;

const ADD = "add";
const DELETE = "DELETE"
const COMPLETE ="COMPLETE"
const Fixed = "Fixed"

const initState = {
  todos: [],
  completed:[],
};

const reducer = (state, action) => {
  switch (action.type) {
    case ADD:
      return { ...state,todos: [...state.todos, { text: action.payload ,id:Math.random()}] };
    case DELETE:
        const deleteTarget = state.todos.filter(item=>item.id !== action.id);
        return{
            ...state,
            todos:[...deleteTarget]
        }
    case COMPLETE:
        const original = state.todos.filter(item=>item.id !== action.id)
        const completed = state.todos.filter(item=>item.id === action.id)
        return{
            ...state,
            todos:[...original],
            completed:[...state.completed,...completed]
        }
    case Fixed:
        const fixed = state.completed.map(item=>item.id===action.id?{...item,fixed:true}:item);
        return {
            ...state,
            completed:[...fixed]
        }
    case "nofixed":
        const nofixed = state.completed.map(item=>item.id===action.id?{...item,fixed:false}:item);
        return{
            ...state,
            completed:[...nofixed]
        }
    default:
      throw console.log("nodatas")
  }
};

const Todo = () => {
  const [newTodo, setNewTodo] = useState("");
  const [state, dispatch] = useReducer(reducer, initState);
  const onchange = (event) => {
    setNewTodo(event.target.value);
  };
  const onsubmit = (event) => {
    event.preventDefault();
    if (newTodo === "") {
      return;
    }
    dispatch({ type: ADD, payload: newTodo });
    setNewTodo("");
  };
  console.log(
    state.completed.map((item) => item),
    "Î≥¥Ïó¨ÏßÄÎäîÍ≥≥"
  );
  console.log(
    state.todos.map((item) => item),
    "Ìà¨ÎëêÏä§ Î≥¥Ïó¨ÏßÄÎäîÍ≥≥"
  );
  return (
    <>
        <Hone>TODO APP</Hone>
        <form onSubmit={onsubmit}>
            <input style={{marginBottom:20}} type="text" onChange={onchange} value={newTodo} />
        </form>
        <div>
        {state.todos.length<1?"":(
            <Ul>
                <Title>Todoapp</Title>
                {
                    state.todos.map((item,idx) => (
                        <Li key={item.id}>
                    <Hone>{idx+1}Î≤à | {item.text}</Hone>
                    <Button onClick={()=>dispatch({type:DELETE ,id:item.id})} >‚ùå</Button>
                    <Button onClick={()=>dispatch({type:COMPLETE,id:item.id})} >‚úÖ</Button>
                </Li>)
                )
            }</Ul>
        )}
        {state.completed && state.completed.length<1?"":(
            <>
                <Title>Completed</Title>
                {state.completed.map((item,idx)=>(
                    <Li style={{backgroundColor:item.fixed?"orange":""}} key={item.id} >
                        <Hone>{idx+1}Î≤à | {item.text}</Hone>
                        <Button onClick={()=>dispatch({type:item.fixed?"nofixed":Fixed ,id:item.id})} >üìå</Button>
                        <Button>üÖæÔ∏è</Button>
                    </Li>
                ))}
            </>
        )}
        </div>

    </>
  );
};

export default Todo;
